import React, { Component } from 'react';
import Modal from 'react-modal';

import './NoteList.css';

import ListGroupItem from 'react-bootstrap/lib/ListGroupItem';
import ListGroup from 'react-bootstrap/lib/ListGroup';
import Button from 'react-bootstrap/lib/Button';
import FormControl from 'react-bootstrap/lib/FormControl';


class NoteList extends Component {

    state = {
        modalIsOpen: false,
        notesList: [],
        newNote: '',
        selectedNote: 0
    };

    componentDidMount() {
        const state = localStorage.getItem('state')
        this.setState({
            ...this.state,
            notesList: JSON.parse(state)
        })
    }


    openModalAddNote = () => {
        this.setState({
            ...this.state,
            modalIsOpen: true
        });
    }

    inputHandler = (event) => {
        this.setState({
            ...this.state,
            newNote: event.target.value
        })
    }

    addNote = (name) => {
        if (name.trim() === '') {
            alert('Напишите хоть что-нибудь ))')
            return;
        }
        const newNote = { name: name, text: '' };
        const oldNotesList = [...this.state.notesList];
        oldNotesList.push(newNote);
        this.setState(() => ({
            ...this.state,
            notesList: this.state.notesList.concat(newNote),
            newNote: ''
        }), () => this.saving());
    }

    deleteNote = () => {
        const oldNotesList = [...this.state.notesList];
        const index = this.state.selectedNote;
        oldNotesList.splice(index, 1)
        this.setState(() => ({
            ...this.state,
            notesList: oldNotesList
        }), () => this.saving());
    }

    closeModal = () => {
        this.setState({
            ...this.state,
            modalIsOpen: false
        })
    }

    selectNode = (index) => {
        this.setState({
            ...this.state,
            selectedNote: index
        })
    }

    textareaHandler = (event) => {
        const index = this.state.selectedNote;
        const oldNotesList = [...this.state.notesList];
        oldNotesList[index].text = event.target.value;
        this.setState(() => ({
            ...this.state,
            notesList: oldNotesList
        }), () => this.saving());
    }

    saving = () => {
        localStorage.setItem('state', JSON.stringify(this.state.notesList));
    }

    handleKeyPress = (event) => {
        if(event.key === 'Enter'){
          this.addNote(this.state.newNote);
        }
      }

    render() {
        let notes = this.state.notesList.map((note, index) => {
            if (this.state.selectedNote === index) return <ListGroupItem key={index}
                onClick={() => this.selectNode(index)}
                active>{note.name} </ListGroupItem>
            return <ListGroupItem key={index} onClick={() => this.selectNode(index)}>{note.name}</ListGroupItem>
        })

        let noteText = null;
        this.state.notesList.length !== 0 && this.state.notesList[this.state.selectedNote] ? noteText = this.state.notesList[this.state.selectedNote].text : noteText = null;

        let noteEditor = null;
        this.state.notesList.length !== 0 && this.state.notesList[this.state.selectedNote] ? noteEditor = (<FormControl componentClass="textarea" rows="10" cols="45" maxLength="150" value={noteText} onChange={(event) => this.textareaHandler(event)}/>) : noteEditor = null;


        return (
            <div className="NotesList">
                <Button onClick={this.openModalAddNote} bsStyle="success">Добавить</Button>
                <Button onClick={this.deleteNote} bsStyle="danger">Удалить</Button>
                <Modal
                    isOpen={this.state.modalIsOpen}
                    className={{base: 'Modal'}}
                    ariaHideApp={false}>
                    <div className="modalHeader"><strong>Добавление новой заметки</strong> </div>
                    <div className="modalDescription">
                        <FormControl placeholder="Введите название новой заметки"  onKeyPress={this.handleKeyPress}   onChange={(event) => this.inputHandler(event)} value={this.state.newNote} />
                        <div className="modalFooter">
                            <Button onClick={() => this.addNote(this.state.newNote)} bsStyle="success">Добавить</Button>
                            <Button onClick={this.closeModal} bsStyle="danger">Закрыть</Button>
                        </div>
                    </div>
                </Modal>
                <div className="nodesBlock">
                    <div className="listGroup">
                        <ListGroup>
                            {notes}
                        </ListGroup>
                    </div>
                    <div className="noteEditor">
                        {noteEditor}
                    </div>
                </div>
            </div>
        )
    }
}


export default NoteList;


